import React from 'react'
import { connect } from 'react-redux'
import { setNotification } from '../../reducers/notificationReducer'
import emailService from '../../services/email'
import { Container, Row, Col, Form, Button } from 'react-bootstrap'
import { Formik } from 'formik'
import * as Yup from 'yup'
import PropTypes from 'prop-types'

const ContactForm = ({ setNotification }) => {

	const messageFormSchema = Yup.object().shape({
		name: Yup.string()
			.min(2, ' 2 .')
			.max(30, ' 30 .')
			.required(' \'?'),
		email: Yup.string()
			.email('   .')
			.required('   .'),
		message: Yup.string()
			.max(280, ' 280 .')
			.required(' ,   .')
	})

	const sendContactMessage = async values => {
		await emailService.sendContactEmail(values)
			.then(() => {
				setNotification({
					message: '  ,  .',
					variant: 'success'
				}, 5)
			})
			.catch(error => {
				setNotification({
					message: ` ,      ,  : ${error.message}`,
					variant: 'danger'
				}, 5)
			})
	}

	return (
		<Container className="py-4">
			<Row className="d-flex justify-content-center">
				<Col sm={10} md={7} lg={6}>
					<h1 className="text-center custom-font pb-4"> &apos;</h1>
					<Formik
						initialValues={{
							name: '',
							email: '',
							message: ''
						}}
						onSubmit={async (values, { resetForm }) => {
							await sendContactMessage(values)
							resetForm()
						}}
						validationSchema={messageFormSchema}
					>
						{({ handleSubmit,
							handleChange,
							handleBlur,
							values,
							touched,
							errors
						}) => (
							<Form
								data-cy="contact-form"
								noValidate
								onSubmit={handleSubmit}
							>

								{/* Message sender name input */}
								<Form.Row>
									<Form.Group controlId="name-input" as={Col}>
										<Form.Label>
											 &apos;
											<span className="required-text">*</span>
										</Form.Label>
										<Form.Control
											type="text"
											name="name"
											data-cy="name-input"
											onChange={handleChange}
											onBlur={handleBlur}
											value={values.name}
											isValid={touched.name && !errors.name}
											isInvalid={touched.name && !!errors.name}
										/>
										<Form.Control.Feedback>
											Ok
										</Form.Control.Feedback>
										<Form.Control.Feedback type="invalid">
											{errors.name}
										</Form.Control.Feedback>
									</Form.Group>
								</Form.Row>

								{/* Message sender email input */}
								<Form.Row>
									<Form.Group controlId="email-input" as={Col}>
										<Form.Label>
											  
											<span className="required-text">*</span>
										</Form.Label>
										<Form.Control
											type="email"
											name="email"
											data-cy="email-input"
											onChange={handleChange}
											onBlur={handleBlur}
											value={values.email}
											isValid={touched.email && !errors.email}
											isInvalid={touched.email && !!errors.email}
										/>
										<Form.Control.Feedback>
											Ok
										</Form.Control.Feedback>
										<Form.Control.Feedback type="invalid">
											{errors.email}
										</Form.Control.Feedback>
									</Form.Group>
								</Form.Row>

								{/* Message body input */}
								<Form.Row>
									<Form.Group controlId="message-input" as={Col}>
										<Form.Label>
											 
											<span className="required-text">*</span>
										</Form.Label>
										<Form.Control
											as="textarea"
											name="message"
											rows="6"
											data-cy="message-input"
											onChange={handleChange}
											onBlur={handleBlur}
											value={values.message}
											isValid={touched.message && !errors.message}
											isInvalid={touched.message && !!errors.message}
										/>
										<Form.Control.Feedback>
											Ok
										</Form.Control.Feedback>
										<Form.Control.Feedback type="invalid">
											{errors.message}
										</Form.Control.Feedback>
									</Form.Group>
								</Form.Row>

								{/* Button */}
								<Form.Row className="d-flex justify-content-center">
									<Button
										type="submit"
										variant="primary"
										data-cy="contact-msg-btn"
										className="primary-color-shadow px-5"
									>
										
									</Button>
								</Form.Row>
							</Form>
						)}
					</Formik>
				</Col>
			</Row>
		</Container>
	)
}

const mapStateToProps = (state) => {
	return {
		user: state.user
	}
}

const mapDispatchToProps = {
	setNotification
}

ContactForm.propTypes = {
	setNotification: PropTypes.func.isRequired
}

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(ContactForm)
